/* eslint-disable jsx-a11y/label-has-associated-control */
import { useContext } from 'react';

import {
  Avatar,
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  Divider,
  Typography
} from '@material-ui/core';
import uploadImg from 'src/API/filesAPI';
import { updateStudentAvatar } from 'src/API/studentAPI';
import { UserContext } from '../../../API/auth';

const StudentProfile = (props) => {
  const { user, setUser } = useContext(UserContext);
  const handleAddfile = async (event) => {
    const img = event.target.files[0];
    const res = await uploadImg(img, Date.now() + img.name);
    const updateAvatar = await updateStudentAvatar(
      user.id,
      `/static/images/avatars/${res}`
    );
    setUser(updateAvatar);
  };
  return (
    <Card {...props}>
      <CardContent>
        <Box
          sx={{
            alignItems: 'center',
            display: 'flex',
            flexDirection: 'column'
          }}
        >
          <Avatar
            src={user.avatar}
            sx={{
              height: 100,
              width: 100
            }}
          />
          <Typography color="textPrimary" gutterBottom variant="h3">
            {` ${user.fname} ${user.lname}`}
          </Typography>
        </Box>
      </CardContent>
      <Divider />
      <CardActions style={{ justifyContent: 'center' }}>
        <form
          autoComplete="off"
          noValidate
          encType="multipart/form-data"
          {...props}
        >
          <input
            style={{ display: 'none' }}
            accept="image/*"
            id="contained-button-file"
            multiple
            type="file"
            onChange={handleAddfile}
          />
          <label htmlFor="contained-button-file">
            <Button
              variant="contained"
              color="primary"
              component="span"
              fullWidth
            >
              Upload picture
            </Button>
          </label>
        </form>
      </CardActions>
    </Card>
  );
};

export default StudentProfile;
